.projects
{
  color: white;
}
@media only screen and (min-width: 601px) {
  
  .App {
    text-align: center;
    
  }
  
  .App-logo {
    height: 30vmin;
    pointer-events: none;
    border-radius: 50%;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 2s linear;
    }
  }
  
  .App-header {
    background-image: url("./images/porto.jpg");
    background-size:cover;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  /* Add a black background color to the top navigation */
  .topnav {
    background-color: darkslategrey;
    overflow: hidden;
    position: fixed;
    width:100%;
    z-index: 100;
    opacity: 75%;
  }
  
  /* Style the links inside the navigation bar */
  .topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 30px 30px;
    text-decoration: none;
    font-size: 30px;
  }
  
  /* Change the color of links on hover */
  .topnav a:hover {
    background-color: #ddd;
    color: black;
    transition: 1s;
  }
  
  
  /* Style the logo in the navbar */
  .topnav img {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    width:75px;
  }
  
  .slideshow img {
    width:800px;
    margin: 10px;
  
  }
  
  html{
    background-color: #282c34;
      scroll-behavior: smooth;
  }
  
  #about {
    color:#f2f2f2;
    margin:10%;
    font-size: 25px;
    line-height: 1.5;
  }
  .projects {
    color:#f2f2f2;
    margin:10%;
    font-size: 25px;
    line-height: 1.5;
  }
  
  .socials a {
    font-size: 100px;
    color:#f2f2f2;
  }
  
  .socials a:hover{
    color: #61dafb;
    transition: 1s;
  }
  td {
    padding:20px;
  }
  footer
  {
    color: white;;
  }
    
}


@media only screen and (max-width: 600px) {
  
  .App {
    text-align: center;
    
  }
  
  .App-logo {
    height: 30vmin;
    pointer-events: none;
    border-radius: 50%;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 2s linear;
    }
  }

  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  /* Add a black background color to the top navigation */
  .topnav {
    background-color: darkslategrey;
    overflow: hidden;
    width:100%;
    z-index: 100;
    opacity: 75%;
  }
  
  /* Style the links inside the navigation bar */
  .topnav a {
    color: #f2f2f2;
    text-align: center;
    padding: 10px 10px;
    text-decoration: none;
    font-size: 20px;
    position: relative;
    display: inline-block;
  }
  
  /* Change the color of links on hover */
  .topnav a:hover {
    background-color: #ddd;
    color: black;
    transition: 1s;
  }
  .App-header{
    margin-bottom: auto;
  }
  
  /* Style the logo in the navbar */
  .topnav img {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 5px 5px;
    text-decoration: none;
    font-size: 17px;
    width:75px;
  }
  
  .slideshow img {
    width:100%;
  
  }
  
  html{
    background-color: #282c34;
      scroll-behavior: smooth;
      width: 100%;
  }
  
  #about {
    color:#f2f2f2;
    margin:5%;
    font-size: 10px;
    line-height: 1.5;
    margin-bottom: 20vh;
  }
  
  .socials a {
    font-size: 100px;
    color:#f2f2f2;
  }
  
  .socials a:hover{
    color: #61dafb;
    transition: 1s;
  }
  td {
    padding:10px;
  }
  .resume {
    width:100%;
    height:55vh;
  }
  footer
  {
    color: white;;
  }
    
}

